import { Inject, Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { FILTER_POINT_STATUS, IPoint, IPointStatus } from '../models/pickup-points.model';
import { cities } from '../data/pickup-points-mock';
import { CompanyService } from '@company/company.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map} from 'rxjs/operators';

export type pointsActions = 'add' | 'edit' | 'delete';

@Injectable({
  providedIn: 'root'
})
export class PickupPointsService {

  onPointsChanged = new Subject<{ id?: number, action: pointsActions, point?: IPoint }>();

  private STATUSES = [
    {
      label: 'JMART.SETTINGS.PICKUPPOINTS.LISTS.ALL',
      value: FILTER_POINT_STATUS.ALL,
      count: 0,
      classList: 'badge-primary',
      isSelected: true,
      isDisabled: false,
    },
    {
      label: 'JMART.SETTINGS.PICKUPPOINTS.LISTS.SHOP',
      value: FILTER_POINT_STATUS.SHOP,
      count: 0,
      classList: 'badge-primary',
      isSelected: false,
      isDisabled: false,
    },
    {
      label: 'JMART.SETTINGS.PICKUPPOINTS.LISTS.WAREHOUSE',
      value: FILTER_POINT_STATUS.WAREHOUSE,
      count: 0,
      classList: 'badge-light',
      isSelected: false,
      isDisabled: false,
    },
    {
      label: 'JMART.SETTINGS.PICKUPPOINTS.LISTS.MAIN_WAREHOUSE',
      value: FILTER_POINT_STATUS.MAIN_WAREHOUSE,
      count: 0,
      classList: 'badge-primary',
      isSelected: false,
      isDisabled: false,
    },
    {
      label: 'JMART.SETTINGS.PICKUPPOINTS.LISTS.COUNTRY_WAREHOUSE',
      value: FILTER_POINT_STATUS.COUNTRY_WAREHOUSE,
      count: 0,
      classList: 'badge-light',
      isSelected: false,
      isDisabled: false,
    }
  ];
  private statuses$ = new BehaviorSubject<IPointStatus[]>(this.STATUSES);

  constructor(
    private companyService: CompanyService,
    private http: HttpClient,
    @Inject('JMART_CABINET_BASE_URL') private baseUrl: string,
  ) {}

  getStatuses() {
    return this.statuses$;
  }

  getCities() {
    const url = `${this.baseUrl}/gw/dictionary/v1/cities`;
    return this.http.get<any>(url)
      .pipe(map(res => res.data));
  }

  getPoints() {
    const url = `${this.baseUrl}/gw/company/v2/my/store-location`;
    return this.http.get<any>(url)
      .pipe(map(res => {
        return res.data.data;
      }));
  }

  addPoint(point: IPoint): Observable<any> {
    const url = `${this.baseUrl}/gw/company/v2/my/store-location`;
    return this.http.post<IPoint>(url, point);
  }

  edit(point: IPoint, storeLocationId: string): Observable<any> {
    const url = `${this.baseUrl}/gw/company/v2/my/store-location/${storeLocationId}`;
    return this.http.put<IPoint>(url, point);
  }

  remove(storeLocationId: number): Observable<unknown> {
    const url = `${this.baseUrl}/gw/company/v2/my/store-location/${storeLocationId}`;
    return this.http.delete(url);
  }
}

