import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { JInputComponent } from '@shared/input';
import { MaskedService } from '@shared/masked/masked.service';

export type InputAutocompleteOption<T = any> = {
  title: string,
  value: T
};

@Component({
  selector: 'j-input-autocomplete',
  templateUrl: './input-autocomplete.component.html',
  styleUrls: ['./input-autocomplete.component.scss']
})
export class InputAutocompleteComponent extends JInputComponent implements OnInit, OnDestroy {

  @Input() options: InputAutocompleteOption[] = [];
  @Input() name = 'inputAutocomplete';

  @Output() onSelect = new EventEmitter();
  @Output() inputChange = new EventEmitter();

  inputValue: string = '';

  constructor(
    public maskedService: MaskedService
  ) {
    super(maskedService);
  }

  get isFilled(): boolean {
    const hasAutoFill =
      this.input?.nativeElement &&
      window
        .getComputedStyle(this.input.nativeElement, null)
        .getPropertyValue('appearance') === 'menulist-button';
    return `${this.formControl.value.title}`?.length || hasAutoFill ? true : false;
  }

  onChange(title: string) {
    this.formControl.setValue({
      title,
      value: null,
    });
    this.inputChange.emit();
  }
  
  onOptionSelect(option: InputAutocompleteOption): void {
    this.formControl.setValue(option);
    this.inputValue = option.title;
    this.onSelect.emit(option);
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.inputValue = this.formControl.value.title || '';
    // if (this.formControl.value.title) {
    //   const selectedOption = this.options.find(({ title }) => (
    //     title === this.formControl.value.title
    //   )) || { title: '', value: null };
    //   this.onOptionSelect(selectedOption);
    // }
  }

}
