import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ActivatedRoute } from '@angular/router';
import { CreateDeliveryModalComponent } from '../../modals/create-delivery-modal/create-delivery-modal.component';
import { Observable, of } from 'rxjs';

@Component({
  selector: 'j-delivery',
  templateUrl: './delivery.component.html',
  styleUrls: ['./delivery.component.scss']
})
export class DeliveryComponent implements OnInit {

  translatePrefix = 'JMART.SETTINGS.DELIVERY';

  modalSmParams = {
    backdropClass: 'backdrop_light',
    windowClass: 'modal_light modal_dictionary',
    centered: true
  };

  constructor(private ngbModalService: NgbModal) { }

  ngOnInit(): void {}

  addDelivery() {
    this.ngbModalService.open(CreateDeliveryModalComponent, this.modalSmParams);
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

}
