import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { deliveryModes, estimatedType } from '@jmart-cabinet/modules/settings/data/delivery-mock';
import { ICity, IType } from '@jmart-cabinet/modules/settings/models/delivery.model';
import { DeliveryService } from '@jmart-cabinet/modules/settings/services/delivery.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'j-create-delivery-form',
  templateUrl: './create-delivery.component.html',
  styleUrls: ['./create-delivery.component.scss']
})
export class CreateDeliveryFormComponent implements OnInit, OnDestroy {

  constructor(
    private fb: FormBuilder,
    private deliveryService: DeliveryService
  ) {}
  
  subscription = new Subscription();

  form: FormGroup;
  isSubmitted = false;
  isCancel = false;

  cities: ICity[];
  cityNames: string[];

  deliveryModes: IType[] = deliveryModes;
  estimatedType = estimatedType;

  formPricesLink = null;

  editedIndex = -1;
  test  = [1,2,3]

  ngOnInit(): void {
    this.form = this.fb.group({
      // deliveryMode: new FormControl('jpost', Validators.required),
      isToday: new FormControl('today', Validators.required),
      estimatedTime: new FormControl('1300', Validators.required),
      workingTime: new FormGroup({
        isDayAndNight: new FormControl(''),
        hour: new FormGroup({
          from: new FormControl(''),
          to: new FormControl(''),
        })
      }),
      prices: new FormArray([])
    });
    
    this.addPrice(),
    this.addPrice(true),

    this.formPricesLink = (<FormArray>this.form.get('prices')).controls;

    const isTodaySub = this.form.get('isToday').valueChanges.subscribe((value) => {
      const action = value !== 'today'
        ? 'disable'
        : 'enable';
      this.form.get('estimatedTime')[action]();
    });
    this.subscription.add(isTodaySub);
    
    const workingTimeSub = this.form.get('workingTime.isDayAndNight').valueChanges.subscribe((value) => {
      const action = value
        ? 'disable'
        : 'enable';
      const hourGroup = this.form.get('workingTime.hour') as FormGroup;  
      Object.keys(hourGroup.controls).forEach(
        key => hourGroup.controls[key][action]()
      );
    });

    this.subscription.add(workingTimeSub);
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  addPrice(canBeFree = false) {
    const isDeliveryTypeCheckbox = canBeFree ? false : '';
    (this.form.get('prices') as FormArray).push(new FormGroup({
      from: new FormControl(''),
      to: new FormControl(isDeliveryTypeCheckbox),
      deliveryPrice: new FormControl(''),
    }));
  }

  // get disabeEstimatedTime() {
  //   console.log(this.form.get('isToday').value !== 'today')
  //   return this.form.get('isToday').value !== 'today';
  // }

  get formTitle() {
    const formTitleKey = this.editedIndex === -1 ? 'ADD' : 'EDIT';
    return `JMART.SETTINGS.DELIVERY.${formTitleKey}`; 
  }

  get cityError() {
    return this.form.get('city').errors;
  }

  selectedCity(city: string) {
    this.form.get('city').setValue(city);
  }

  save() {

  }

  remove() {}

  cancel() {
  }

}
