import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'maskPrefixPhone'
})
export class MaskPrefixPhonePipe implements PipeTransform{
  transform(value: string): string {
    if(value.length === 10) {
      return '+7' + ' ('
        + value.slice(0, 3) + ') '
        + value.slice(3,6) + '-'
        + value.slice(6, 8) + '-'
        + value.slice(8, 10)
    }
  }
}
