import { FormControl, FormGroup } from '@angular/forms';
import { IWorkRegime, WORK_DAYS } from '../j-tole-work-regime';

export class WorkingTimeMapper {

    workignTimeFormGroup = null;

    constructor(externalWorkingTime = null) {
        this.workignTimeFormGroup = externalWorkingTime
            ? this.mapExternalToInternalWorkingTime(externalWorkingTime)
            : this.getDefaultWorkingTime();
    }

    private getDefaultWorkingTime(): IWorkRegime[] {
        return [
            {
              form: new FormGroup({
                monday: new FormControl(false),
                tuesday: new FormControl(false),
                wednesday: new FormControl(false),
                thursday: new FormControl(false),
                friday: new FormControl(false),
                saturday: new FormControl(false),
                sunday: new FormControl(false),
              }),
              days: WORK_DAYS,
              workRegimeTime: new FormGroup({
                open: new FormControl('0900'),
                close: new FormControl('1800'),
              }),
              workRegimeBreakTime: new FormGroup({
                from: new FormControl('09:00'),
                to: new FormControl('18:00'),
              }),
              workRegimeOptions: new FormGroup({
                workRegimeTime: new FormControl(false),
                workRegimeBreakTime: new FormControl(false),
              }),
            },
          ];
    }

    public mapExternalToInternalWorkingTime(workingTime) {
        return this.workignTimeFormGroup = workingTime.map((workTime, index) => {
              const newWorkTime = {
                form: new FormGroup({
                  monday: new FormControl(false),
                  tuesday: new FormControl(false),
                  wednesday: new FormControl(false),
                  thursday: new FormControl(false),
                  friday: new FormControl(false),
                  saturday: new FormControl(false),
                  sunday: new FormControl(false),
                }),
                days: WORK_DAYS,
                workRegimeTime: new FormGroup({
                  open: new FormControl('0900'),
                  close: new FormControl('1800'),
                }),
                workRegimeBreakTime: new FormGroup({
                  from: new FormControl('09:00'),
                  to: new FormControl('18:00'),
                }),
                workRegimeOptions: new FormGroup({
                  workRegimeTime: new FormControl(false),
                  workRegimeBreakTime: new FormControl(false),
                }),
              };
      
              const days = {
                '': 'monday',
                '': 'tuesday',
                '': 'wednesday',
                '': 'thursday',
                '': 'friday',
                '': 'saturday',
                '': 'sunday',
              };
      
              const prevWorkingTime = workingTime[index - 1];
              if (prevWorkingTime) {
                prevWorkingTime.days.forEach(({ name, day }) => {
                  const control = newWorkTime.form.get(
                    days[day || name.slice(0, -1)]
                  );
                  control.disable();
                  control.setValue(true);
                });
              }
      
              workTime.days.forEach(({ day, name }) => {
                const enDay = days[day || name.slice(0, -1)];
                newWorkTime.form.get(enDay).setValue(true);
              });
      
              const isDayAngNight =
                workTime.hour.from === '00:00' && workTime.hour.to === '23:59';
      
              if (isDayAngNight) {
                newWorkTime.workRegimeOptions.get('workRegimeTime').setValue(true);
                WorkingTimeMapper.toggleDisabledFromTo(true, newWorkTime);
              }
      
              const [from, to] = isDayAngNight
                ? ['', '']
                : [workTime.hour.from, workTime.hour.to];
      
              newWorkTime.workRegimeTime.get('open').setValue(from);
              newWorkTime.workRegimeTime.get('close').setValue(to);
      
              return newWorkTime;
        });
    }

    public getExternalState() {
        return this.workignTimeFormGroup.map((regTime) => {
            const { open, close } = regTime.workRegimeTime.value;
            let from = open,
              to = close;
      
            const days = Object.entries(regTime.form.controls)
              .filter(
                ([_, { value, disabled }]: [string, FormControl]) =>
                  value && !disabled
              )
              .map(([dayName]) => {
                return {
                  id: {
                    sunday: 0,
                    monday: 1,
                    tuesday: 2,
                    wednesday: 3,
                    thursday: 4,
                    friday: 5,
                    saturday: 6,
                  }[dayName],
                  day: WorkingTimeMapper.mapDay(dayName),
                };
              });
      
            const hour = regTime.workRegimeOptions.value.workRegimeTime
              ? {
                  from: '00:00',
                  to: '23:59',
                }
              : {
                  from: from.indexOf(':') > -1 ? from : from.match(/.{1,2}/g).join(':'),
                  to: to.indexOf(':') > -1 ? to : to.match(/.{1,2}/g).join(':'),
                };
      
            return {
              days,
              hour,
            };
          });
    }

    public static mapDay(value: string) {
        const dayNames = {
            monday: WORK_DAYS[0],
            tuesday: WORK_DAYS[1],
            wednesday: WORK_DAYS[2],
            thursday: WORK_DAYS[3],
            friday: WORK_DAYS[4],
            saturday: WORK_DAYS[5],
            sunday: WORK_DAYS[6],
          };
    
          switch (value) {
            case dayNames.monday:
              return '';
            case dayNames.tuesday:
              return '';
            case dayNames.wednesday:
              return '';
            case dayNames.thursday:
              return '';
            case dayNames.friday:
              return '';
            case dayNames.saturday:
              return '';
            case dayNames.sunday:
              return '';
            default:
              return '';
            }
    }

    public static toggleDisabledFromTo(value: boolean, workTime): void {
        const action = value ? 'disable' : 'enable';
        ['open', 'close'].forEach((controlName) => {
          const control = workTime.workRegimeTime.get(controlName);
          control.setValue('');
          control[action]();
        });
      }

}
