import { Component, Input, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalsService } from '../../../services/modals.service';
import { NotifierService } from 'angular-notifier';

@Component({
  selector: 'j-jmart-settings-logo-modal',
  templateUrl: './logo-modal.component.html',
  styleUrls: ['./logo-modal.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class LogoModalComponent implements OnInit, OnDestroy {
  @Input() smallLogo: string;

  largeLogo: string;
  reader = new FileReader();
  file: File;
  fileInput = document.querySelector('#fileInput');

  constructor(
    private modal: NgbActiveModal,
    private modalService: ModalsService,
    private notifier: NotifierService
  ) { }

  ngOnInit() {
    if(this.smallLogo) {
      this.largeLogo = this.smallLogo;
    } else {
      this.largeLogo = '';
    }
  }

  ngOnDestroy() {
    this.largeLogo = '';
  }

  uploadFile(event: any) {
    if (event.target.files && event.target.files[0]) {
      this.file = event.target.files[0];

      const type = this.file.type;
      if(type.match('image.png') || type.match('image.jpg') || type.match('image.jpeg') || type.match('image.svg')) {
        this.reader.onload = e => this.largeLogo = this.reader.result.toString();
        this.reader.readAsDataURL(this.file);
      } else {
        this.notifier.show({
          type: 'warning',
          message: '    .jpeg, .jpg, .png, .svg'
        });
        this.file = null;
      }
    }
  }

  saveFile() {
    this.modalService.changeUploadedLogo(this.file);
    this.closeModal()
  }

  removeFile() {
    this.closeModal();
    this.file = null;
    this.modalService.changeUploadedLogo();
  }


  dismissModal(error: any = null, imageSrc: string = '') {
    this.modal.dismiss({ error, imageSrc });
    this.largeLogo = '';
  }

  closeModal() {
    this.largeLogo = '';
    this.modal.close();
  }
}
