import { Injectable } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { RemoveModalComponent } from '../modals/information/remove-modal/remove-modal.component';
import { PickupPointsModalComponent } from '../modals/point/pickup-points-modal/pickup-points-modal.component';
import { Subject } from 'rxjs';
@Injectable({
  providedIn: 'root'
})

export class ModalsService {
  public uploadedLogo$ = new Subject<File>();

  modalSmParams = {
    backdropClass: 'backdrop_light',
    windowClass: 'modal_light modal_dictionary',
    centered: true
  };

  constructor(private ngbModalService: NgbModal) {}

  public changeUploadedLogo(file?: File) {
    if(file) {
      this.uploadedLogo$.next(file);
    } else {
      this.uploadedLogo$.next(null);
    }
  }

  addLogoModal(modal, smallLogo?) {
    const modalRef = this.ngbModalService.open(modal, this.modalSmParams);
    modalRef.componentInstance.smallLogo = smallLogo;
  }

  addEditPersonModal(modal, blockId?, info?) {
    const modalRef = this.ngbModalService.open(modal, this.modalSmParams);
    modalRef.componentInstance.blockId = blockId;
    modalRef.componentInstance.info = info;
  }

  removePersonModal(blockId, id) {
    const modalRef = this.ngbModalService.open(RemoveModalComponent, this.modalSmParams);
    modalRef.componentInstance.blockId = blockId;
    modalRef.componentInstance.id = id;
  }

  pickupPointsModal(point?) {
    const modalRef = this.ngbModalService.open(PickupPointsModalComponent, this.modalSmParams);
    modalRef.componentInstance.point = point;
  }
}
