import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SettingsComponent, } from './settings.component';
import {
  InformationComponent,
  DeliveryComponent,
  PickupPointsComponent,
  ScheduleComponent,
  UsersComponent
} from './containers';
import { DELIVERY_CATEGORY } from './models/delivery.model';
import { DeliveryListComponent } from './components';
import { JmartCabinetLayoutComponent } from '@jmart-cabinet/components/jmart-cabinet-layout/jmart-cabinet-layout.component';

const routes: Routes = [
  {
    path: '',
    component: JmartCabinetLayoutComponent,
    children: [
      {
        path: '',
        component: SettingsComponent,
        data: {
          title: 'SETTINGS.TITLE',
        },
        children: [
          // {
          //   path: 'information',
          //   component: InformationComponent,
          // },
          // {
          //   path: 'pickup-points',
          //   component: PickupPointsComponent
          // },
          {
            path: 'delivery',
            component: DeliveryComponent,
            children: [
              {
                path: 'city',
                component: DeliveryListComponent,
                data: {
                  category: DELIVERY_CATEGORY.BY_CITY,
                }
              },
              {
                path: 'intercity',
                component: DeliveryListComponent,
                data: {
                  category: DELIVERY_CATEGORY.BY_INTERCITY,
                }
              },
              {
                path: '**',
                redirectTo: 'city',
              },
            ]
          },
          {
            path: 'schedule',
            component: ScheduleComponent
          },
          {
            path: 'users',
            component: UsersComponent
          },
          {
            path: '**',
            redirectTo: 'delivery',
          },
        ],
      },
    ],
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SettingsRoutingModule {}
