import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';

export type DeliveryPrice = {
  from: FormControl,
  to: FormControl,
  deliveryPrice: FormControl
};

@Component({
  selector: 'j-delivery-price',
  templateUrl: './delivery-price.component.html',
  styleUrls: ['./delivery-price.component.scss']
})
export class DeliveryPriceComponent implements OnInit, OnDestroy {

  @Input() class: string = '';
  @Input() price: FormGroup = null;
  @Input() canBeRemoved: boolean;
  @Input() isLast: boolean = false;

  @Output() remove = new EventEmitter();

  controls: DeliveryPrice;
  checkbox;
  checkboxChangeSub: Subscription;

  constructor() { }

  ngOnInit(): void {
    this.controls = this.price.controls as DeliveryPrice;
    if (!this.showToInput) {
      this.checkbox = new FormControl(false);
      this.checkboxChangeSub = this.checkbox.valueChanges.subscribe((value) => {
        this.controls.deliveryPrice.setValue(value ? '0' : '');
      });
    }
  }

  ngOnDestroy(): void {
    if (!this.showToInput) {
      this.checkboxChangeSub.unsubscribe();
    }
  }

  get showToInput() {
    return [ 'deliveryPrice', 'to' ].every(controlName => String(this.controls[controlName].value) !== '0');
  }

  onRemove() {
    this.remove.emit();
  }

}
