import { Component, DoCheck, Input, OnDestroy, OnInit } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ICity, IPoint, IPointType } from '../../../models';
import { pointsTypes } from '../../../data/pickup-points-mock';
import { PickupPointsService } from '../../../services/pickup-points.service';
import { CompanyService } from '@company/company.service';
import { PointsRemoveModalComponent } from '../points-remove-modal/points-remove-modal.component';
import { WorkingTimeMapper } from '@jmart-cabinet/modules/settings/components/j-tole-work-regime/classes/working-time-mapper.class';

@Component({
  selector: 'j-pickup-points-modal',
  templateUrl: './pickup-points-modal.component.html',
  styleUrls: ['./pickup-points-modal.component.scss']
})
export class PickupPointsModalComponent implements OnInit, OnDestroy {
  @Input() public point;

  form: FormGroup;
  isSubmitted = false;
  isCancel = false;
  isSave = false;

  cityNames: string[];

  translatePrefix = 'JMART.SETTINGS.PICKUPPOINTS';

  typePointList: IPointType[] = pointsTypes;

  editedIndex = -1;

  modalSmParams = {
    backdropClass: 'backdrop_light',
    windowClass: 'modal_light modal_dictionary',
    centered: true
  };

  workingTime = new WorkingTimeMapper();

  constructor(
    private ngbActiveModal: NgbActiveModal,
    private ngbModalService: NgbModal,
    private fb: FormBuilder,
    private pointService: PickupPointsService,
    private companyService: CompanyService
    ) {
    /* Form initialization */
    this.form = this.fb.group({
      status: new FormControl('A', Validators.required),
      companyStoreId: new FormControl('', Validators.required),
      name: new FormControl('', Validators.required),
      city: new FormControl('', Validators.required),
      pickupAddress: new FormControl({ value: '', disabled: true }, Validators.required),
      iban: new FormControl(''),
      type: new FormControl('shop', Validators.required),
      isMain: new FormControl(''),
      isMainInCity: new FormControl('')
    });

    this.form.get('city').valueChanges.subscribe(val => {
      if(val) this.form.get('pickupAddress').enable();
    });
  }

  ngOnInit(): void {

    /*Form draft pre-Save*/
    const draft = sessionStorage.getItem('form');

    /* Filling in a form */
    if(this.point) {
      this.editedIndex = 1;
      const form = this.point;
      this.form.patchValue({
        companyStoreId: form.companyStoreId,
        status: form.status,
        name: form.name,
        city: form.city.name,
        pickupAddress: form.pickupAddress,
        iban: form.iban,
        type: form.type.value,
        isMain: form.isMain,
        isMainInCity: form.isMainInCity,
      });
    } else {
      if(draft) {
        const form = JSON.parse(draft);
        this.form.patchValue({
          companyStoreId: form.companyStoreId,
          status: form.status,
          name: form.name,
          city: form.city.name,
          pickupAddress: form.pickupAddress,
          iban: form.iban,
          type: form.type,
          isMain: form.isMain,
          isMainInCity: form.isMainInCity,
        });
      }
    }
  }

  ngOnDestroy(): void {
    if(this.editedIndex === -1) {
      sessionStorage.setItem('form', JSON.stringify(this.form.value));

      if(this.isCancel || this.isSave) {
        sessionStorage.removeItem('form');
      }
    }
  }

  test() {
    console.log('asd');
    console.log(this.form.get('city').value);
  }

  save() {
    this.isSave = true;
    this.isSubmitted = true;
    if(this.form.invalid) {
      return;
    }

    if(this.editedIndex === -1) {
      const point: IPoint = {
        status: 'A',
        companyStoreId: this.form.value.companyStoreId,
        name: this.form.value.name,
        cityId: this.form.get('city').value.cityId,
        pickupAddress: this.form.value.pickupAddress,
        latitude: 43.3444,
        longitude: 23.445,
        // workingTime: null,
        type: this.form.value.type,
        isMain: this.form.value.isMain ? 'Y' : 'N',
        isMainInCity: this.form.value.isMainInCity ? 'Y' : 'N',
        iban: this.form.value.iban,
        workingTime: this.workingTime.getExternalState(),
      };
      this.pointService.addPoint(point).subscribe((res) => {
        this.pointService.onPointsChanged.next({
          action: 'add',
          point: res.data
        })
      });
    } else {
      const point: IPoint = {
        status: this.form.value.status,
        companyStoreId: this.form.value.companyStoreId,
        name: this.form.value.name,
        cityId: this.form.get('city').value.cityId,
        pickupAddress: this.form.value.pickupAddress,
        latitude: 43.3444,
        longitude: 23.445,
        // workingTime: null,
        type: this.form.value.type,
        isMain: this.form.value.isMain ? 'Y' : 'N',
        isMainInCity: this.form.value.isMainInCity ? 'Y' : 'N',
        iban: this.form.value.iban ? this.form.value.iban : '',
        workingTime: this.workingTime.getExternalState(),
      };
      this.pointService.edit(point, this.point.storeLocationId).subscribe((res) => {
        this.pointService.onPointsChanged.next({
          id: this.point.storeLocationId,
          action: 'edit',
          point: res.data
        })
      });
    }
    this.cancel();
  }

  remove() {
    const modalRef = this.ngbModalService.open(PointsRemoveModalComponent, this.modalSmParams);
    modalRef.componentInstance.storeLocationId = this.point.storeLocationId;
    this.cancel();
  }

  cancel() {
    this.isCancel = true;
    this.ngbActiveModal.dismiss();
  }

  get status(): boolean {
    return this.form.get('status').value.includes('A');
  }

  get formTitle() {
    return this.editedIndex === -1 ? this.mapTranslate('ADD') : this.mapTranslate('EDIT')
  }

  get idPointError() {
    return this.form.get('companyStoreId').errors;
  }

  get titleError() {
    return this.form.get('name').errors;
  }

  get cityError() {
    return this.form.get('city').errors;
  }

  get addressError() {
    return this.form.get('pickupAddress').errors;
  }

  get ibanError() {
    return this.form.get('iban').errors;
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  selectedCity(city: string) {
    this.form.get('city').setValue(city);
  }

  changeStatus(val) {
    let statusValue = val.target.value === 'true' ? 'A' : 'D';
    this.form.get('status').setValue(statusValue);
  }
}
