import { Component, Input, OnInit } from '@angular/core';
import { IPoint } from '../../models';
import { ModalsService } from '../../services';

@Component({
  selector: 'j-points-table',
  templateUrl: './points-table.component.html',
  styleUrls: ['./points-table.component.scss']
})
export class PointsTableComponent implements OnInit {
  @Input() points: IPoint[];
  @Input() search: string;

  translatePrefix = 'JMART.SETTINGS.PICKUPPOINTS';

  constructor(private modalsService: ModalsService) { }

  ngOnInit(): void {
  }

  identify(index, item) {
    return item.storeLocationId;
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  edit(point: IPoint) {
    this.modalsService.pickupPointsModal(point);
  }
}
