import { Component, Input, OnInit } from '@angular/core';
import { LogoModalComponent } from '../../modals';
import { ModalsService } from '../../services/modals.service';

@Component({
  selector: 'j-information-logo',
  templateUrl: './information-logo.component.html',
  styleUrls: ['./information-logo.component.scss']
})
export class InformationLogoComponent implements OnInit {
  @Input() smallLogo: string;

  constructor(
    private modalService: ModalsService
  ) { }

  ngOnInit(): void {
  }

  addLogo() {
    this.modalService.addLogoModal(LogoModalComponent, this.smallLogo);
  }

  get tooltip() {
    if(this.smallLogo) {
      return 'JMART.SETTINGS.INFORMATION.UPLOAD_NEW_LOGO'
    } else {
      return 'JMART.SETTINGS.INFORMATION.UPLOAD_LOGO'
    }
  }
}
