import { IStatus } from '@shared/models';

export interface ICity {
  cityId: number,
  cityName: string
}

export interface IDay {
  id: number,
  day: string,
  name: string,
}

export interface IHour {
  from: string,
  to: string
}

export interface ITime {
  days: IDay[],
  hour: IHour,
  break?: IHour
}

export interface IPoint {
  storeLocationId?: number,
  companyId?: string,
  status?: string,
  companyStoreId: string,
  name: string,
  city?: any,
  cityId: number,
  pickupAddress: string,
  latitude: number,
  longitude: number,
  iban?: string
  type: string,
  isMain: string,
  isMainInCity: string,
  workingTime?: ITime[]
}

export interface IPointType {
  value: string,
  label: string,
  checked?: boolean
}

export enum FILTER_POINT_STATUS {
  ALL = 'all',
  SHOP = 'shop',
  WAREHOUSE = 'warehouse',
  MAIN_WAREHOUSE = 'mainWarehouse',
  COUNTRY_WAREHOUSE = 'countryWarehouse'
}

export enum POINT_TYPE {
  SHOP = 'shop',
  WAREHOUSE = 'warehouse'
}

export enum WAREHOUSE_TYPE {
  DEFAULT = 'none',
  MAIN_WAREHOUSE = 'mainWarehouse',
  COUNTRY_WAREHOUSE = 'countryWarehouse'
}

export interface IPointStatus extends IStatus {
  value: FILTER_POINT_STATUS
}

export interface IPointScheme {
  pointStatus?: FILTER_POINT_STATUS,
  pointSearch?: string
}
