import { Component, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { DeliveryService } from '../../services/delivery.service';


@Component({
    selector: 'j-remove-delivery-modal',
    templateUrl: './remove-delivery-modal.component.html',
    styleUrls: ['./remove-delivery-modal.component.scss'],
})
export class RemoveDeliveryModalComponent {

    @Input() deliveryId;

    constructor(
        public modal: NgbActiveModal,
        private deliveryService: DeliveryService,
    ) {}

    close() {
        this.modal.dismiss();
    }

    remove() {
        this.deliveryService.removeDelivery(this.deliveryId, this.modal);
    }

}
