import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';
import { CreateDeliveryModalComponent } from '../../modals/create-delivery-modal/create-delivery-modal.component';
import { RemoveDeliveryModalComponent } from '../../modals/remove-delivery-modal/remove-delivery-modal.component';
import { IDelivery } from '../../models/delivery.model';
import { NumberSpaceFormatPipe } from '../../pipes/number-space-format.pipe';
import { getRuDays } from '../../utils/getRuDays';
import { JTableClasses, JTableData } from '../j-table/j-table.component';

@Component({
  selector: 'j-delivery-table',
  templateUrl: './delivery-table.component.html',
  styleUrls: ['./delivery-table.component.scss'],
})
export class DeliveryTableComponent implements OnInit, OnChanges {
  @Input() deliveries: any[];

  deliveryTableData: JTableData;
  deliveryTableClasses: JTableClasses = {
    th: 'p-3',
    td: 'p-3',
  };

  translatePrefix = 'JMART.SETTINGS.DELIVERY';

  constructor(
    private translateService: TranslateService,
    private ngbModalService: NgbModal,
    private numberSpaceFormatPipe: NumberSpaceFormatPipe
  ) {}

  ngOnInit(): void {
    const translate = this.translateService.instant(this.translatePrefix);
    const rows = this.deliveries.map((delivery, index) => this.mapDelivery(delivery, index));
    const columns = ['CITY', 'PRICE', 'TIME', 'ESTIMATED_TIME', 'ESTIMATED_MODE'].map(key => translate[key]);
    
    columns.push('');
    this.deliveryTableData = {
      columns,
      rows,
    };
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.deliveryTableData) {
      this.deliveryTableData.rows = changes.deliveries.currentValue.map((delivery, index) => this.mapDelivery(delivery, index));
    }
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  edit(data: { delivery: IDelivery }) {
    const modalRef = this.ngbModalService.open(CreateDeliveryModalComponent);
    modalRef.componentInstance.editDelivery = data;
  }

  remove({ delivery: { id } }: { delivery: IDelivery, index: number }) {
    const modalRef = this.ngbModalService.open(RemoveDeliveryModalComponent);
    modalRef.componentInstance.deliveryId = id;
  }

  mapDelivery( delivery: IDelivery, index: number) {

    const {
      isJpost,
      isCity,
      direction: { from, to },
      attributes,
    } = delivery;

      const city: { from: string, to?: string } = { from: from.title };
      if (to && !isCity) city.to = to.title;
      const jpostTitle = isJpost ? 'JPost' : '';

      if (isJpost) {
        return [
          city,
          null,
          null,
          null,
          jpostTitle,
          { delivery, index },
        ]
      }

      const {
        prices,
        workingTime,
        estimatedTime,
        isToday,
        deliveryEstimatedDay,
      } = attributes;

      return [
        city,
        prices.map(p => {
          return {
            price: parseInt(p.deliveryPrice as string) === 0 ? `` : `${this.numberSpaceFormatPipe.transform(p.deliveryPrice)}`,
            range: `  ${this.numberSpaceFormatPipe.transform(p.from)} ${parseInt(p.deliveryPrice as string) !== 0 ? ` ${this.numberSpaceFormatPipe.transform(p.to)}` : ''}`
          }
        }),
        workingTime.map(t => {
          return {
            days: t.days.map(({ day, name }) => day || name.slice(0, -1)).join(', '),
            hour: t.hour.from.localeCompare('00:00') === 0 && t.hour.to.localeCompare('00:00') === 0
              ? ' '
              : `${t.hour.from} - ${t.hour.to}`
          }
        }),
        {
          ...(isToday && {
            estimatedTime: `  ${estimatedTime}`
          }),
          estimatedType: isToday ? '' : '  ',
          deliveryEstimatedDay: `${deliveryEstimatedDay}${getRuDays(deliveryEstimatedDay)}`,
          isCity
        },
        jpostTitle,
        { delivery, index },
      ];

  }

}
