
export const getRuDays = (value: string, returnValue: boolean = false): string => {
    const daysCount = parseInt(value);
    const n1 = Math.abs(daysCount) % 100; 
    const n2 = n1 % 10;
    if (n1 > 10 && n1 < 20) { return ' '; }
    if (n2 > 1 && n2 < 5) { return ' '; }
    if (n2 == 1) { return ' '; }
    return ' ';
};
