import { Component, Input, OnInit } from '@angular/core';
import { AddEditModalComponent } from '../../modals';
import { IResponsablePerson } from '../../models';
import { ModalsService } from '../../services';

@Component({
  selector: 'j-information-person',
  templateUrl: './information-person.component.html',
  styleUrls: ['./information-person.component.scss']
})
export class InformationPersonComponent implements OnInit {
  @Input() blockId: number;
  @Input() info: IResponsablePerson;
  @Input() isLast: boolean;

  constructor(private modalsService: ModalsService) { }

  ngOnInit(): void {}

  editPerson(info) {
    this.modalsService.addEditPersonModal(AddEditModalComponent, this.blockId, info);
  }

  removePerson(id) {
    this.modalsService.removePersonModal(this.blockId, id);
  }

}
