import { Component, OnInit } from '@angular/core';
import { IResponsablePersonsBlock } from '../../models';
import { ResponsiblePersonsService } from '../../services';

@Component({
  selector: 'j-information-persons',
  templateUrl: './information-persons.component.html',
  styleUrls: ['./information-persons.component.scss']
})
export class InformationPersonsComponent implements OnInit {
  persons: IResponsablePersonsBlock[];

  constructor(private responsiblePersonsService: ResponsiblePersonsService) { }

  ngOnInit(): void {
    this.persons = this.responsiblePersonsService.get();
  }

}
