import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { DELIVERY_MODE, ICity, IType } from '@jmart-cabinet/modules/settings/models/delivery.model';
import { deliveryModes, estimatedType } from '@jmart-cabinet/modules/settings/data/delivery-mock';
import { DeliveryService } from '@jmart-cabinet/modules/settings/services/delivery.service';

@Component({
  selector: 'j-delivery-modal',
  templateUrl: './delivery-modal.component.html',
  styleUrls: ['./delivery-modal.component.scss']
})
export class DeliveryModalComponent implements OnInit {
  @Input() mode: DELIVERY_MODE;

  form: FormGroup;
  isSubmitted = false;
  isCancel = false;

  cities: ICity[];
  cityNames: string[];

  translatePrefix = 'JMART.SETTINGS.DELIVERY';

  deliveryModes: IType[] = deliveryModes;
  estimatedType = estimatedType;

  editedIndex = -1;

  constructor(
    private ngbActiveModal: NgbActiveModal,
    private ngbModalService: NgbModal,
    private fb: FormBuilder,
    private deliveryService: DeliveryService
  ) { }

  ngOnInit(): void {
    this.getCities();

    /* Form initialization */
    this.form = this.fb.group({
      city: new FormControl('', Validators.required),
      deliveryMode: new FormControl('jpost', Validators.required),
      estimatedType: new FormControl('today', Validators.required),
    });
  }

  save() {}

  remove() {}

  cancel() {
    this.ngbActiveModal.dismiss();
  }

  get formTitle() {
    return this.editedIndex === -1 ? this.mapTranslate('ADD') : this.mapTranslate('EDIT')
  }

  getCities() {
    const cities = this.deliveryService.getCities();
    // this.cities = cities;
    // this.cityNames = cities.map((city) => city.cityName);
  }

  get cityError() {
    return this.form.get('city').errors;
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  selectedCity(city: string) {
    this.form.get('city').setValue(city);
  }
}
