import {
  Component,
  DoCheck,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
} from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';

import { Subscription } from 'rxjs';
import { WorkingTimeMapper } from './classes/working-time-mapper.class';

export interface IWorkRegime {
  form: FormGroup;
  days?: string[];
  workRegimeTime?: FormGroup;
  workRegimeBreakTime?: FormGroup;
  workRegimeOptions?: FormGroup;
}

export enum TIME_INPUT_TYPE {
  FROM = 'FROM',
  TO = 'TO',
}

export const WORK_DAYS = [
  'monday',
  'tuesday',
  'wednesday',
  'thursday',
  'friday',
  'saturday',
  'sunday',
];

@Component({
  selector: 'j-tole-work-regime',
  templateUrl: 'j-tole-work-regime.html',
  styleUrls: ['j-tole-work-regime.scss'],
})
export class JToleWorkRegimeComponent implements OnInit, OnDestroy {
  subscription = new Subscription();

  @Input() workRegimes: IWorkRegime[] = [
    {
      form: new FormGroup({
        monday: new FormControl(false),
        tuesday: new FormControl(false),
        wednesday: new FormControl(false),
        thursday: new FormControl(false),
        friday: new FormControl(false),
        saturday: new FormControl(false),
        sunday: new FormControl(false),
      }),
      days: WORK_DAYS,
      workRegimeTime: new FormGroup({
        open: new FormControl('0900'),
        close: new FormControl('1800'),
      }),
      workRegimeBreakTime: new FormGroup({
        from: new FormControl('09:00'),
        to: new FormControl('18:00'),
      }),
      workRegimeOptions: new FormGroup({
        workRegimeTime: new FormControl(false),
        workRegimeBreakTime: new FormControl(false),
      }),
    },
  ];

  pickedDaysCount = 0;
  TIME_INPUT_TYPE = TIME_INPUT_TYPE;

  @Input() showWorkRegimeBreakTime = true;

  constructor() {}

  ngOnInit(): void {
    this.workRegimes.forEach((workTime) =>
      this.subsribeWorkTimeIsDayAndNight(workTime)
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  subsribeWorkTimeIsDayAndNight(workTime: IWorkRegime) {
    const isDayAndNightSub = workTime.workRegimeOptions
      .get('workRegimeTime')
      .valueChanges.subscribe((value) =>
        WorkingTimeMapper.toggleDisabledFromTo(value, workTime)
      );
    this.subscription.add(isDayAndNightSub);
  }

  deleteWorkRegimeHandler(index: number) {
    this.workRegimes.splice(index, 1);
  }

  getDayTranslateKey(day: string) {
    switch (day) {
      case WORK_DAYS[0]:
        return 'WEEK_DAYS.MONDAY';
      case WORK_DAYS[1]:
        return 'WEEK_DAYS.TUESDAY';
      case WORK_DAYS[2]:
        return 'WEEK_DAYS.WEDNESDAY';
      case WORK_DAYS[3]:
        return 'WEEK_DAYS.THURSDAY';
      case WORK_DAYS[4]:
        return 'WEEK_DAYS.FRIDAY';
      case WORK_DAYS[5]:
        return 'WEEK_DAYS.SATURDAY';
      case WORK_DAYS[6]:
        return 'WEEK_DAYS.SUNDAY';
      default:
        return '';
    }
  }

  addWorkRegimesHandler() {
    const newWorkRegimeForm = {
      form: new FormGroup({}),
      days: WORK_DAYS,
      workRegimeTime: new FormGroup({
        open: new FormControl(),
        close: new FormControl(),
      }),
      workRegimeBreakTime: new FormGroup({
        from: new FormControl(),
        to: new FormControl(),
      }),
      workRegimeOptions: new FormGroup({
        workRegimeTime: new FormControl(false),
        workRegimeBreakTime: new FormControl(false),
      }),
    };

    const prevWorkRegime = this.workRegimes[this.workRegimes.length - 1];

    Object.entries(prevWorkRegime.form.controls).forEach((control) => {
      if (control?.[1].value) {
        newWorkRegimeForm.form.addControl(
          control[0],
          new FormControl({
            value: true,
            disabled: true,
          })
        );
      } else {
        newWorkRegimeForm.form.addControl(
          control[0],
          new FormControl({
            value: false,
            disabled: false,
          })
        );
      }
    });

    this.subsribeWorkTimeIsDayAndNight(newWorkRegimeForm);

    if (this.pickedDaysCount !== 7) {
      this.workRegimes.push(newWorkRegimeForm);
    }
  }

  getWorkRegimeSelectedDays(form: FormGroup, workTimeIndex): string {
    const currentWorkRegimeSelectedDays = [];

    const prevWorkTime = this.workRegimes[workTimeIndex - 1];

    Object.entries(form.controls).forEach(
      ([dayName, control]: [string, FormControl]) => {
        if (
          (control.value && !prevWorkTime) ||
          (control.value &&
            prevWorkTime &&
            !prevWorkTime.form.get(dayName).value)
        ) {
          currentWorkRegimeSelectedDays.push(dayName);
        }
      }
    );

    this.pickedDaysCount = currentWorkRegimeSelectedDays.length;

    return currentWorkRegimeSelectedDays.map(day => WorkingTimeMapper.mapDay(day)).join(', ');
  }
}
