import { AnalyticsManagerAbstract } from '@analytics/services/manager';
import { AfterViewInit, Component, Input, OnInit } from '@angular/core';
import {
  FormArray,
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { Router } from '@angular/router';
import { InputAutocompleteOption } from '@jmart-cabinet/components/input-autocomplete/input-autocomplete.component';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { WorkingTimeMapper } from '../../components/j-tole-work-regime/classes/working-time-mapper.class';
import { deliveryModes, estimatedType } from '../../data/delivery-mock';
import {
  DELIVERY_MODE,
  ICity,
  IDelivery,
  IType,
} from '../../models/delivery.model';
import {
  CityAndRegionOption,
  DeliveryService,
} from '../../services/delivery.service';
import { getRuDays } from '../../utils/getRuDays';

@Component({
  selector: 'j-create-deivery-modal',
  templateUrl: './create-delivery-modal.component.html',
  styleUrls: ['./create-delivery-modal.component.scss'],
})
export class CreateDeliveryModalComponent implements OnInit {

  modalSmParams = {
    backdropClass: 'backdrop_light',
    windowClass: 'modal_light modal_dictionary',
    centered: true,
  };

  @Input() editDelivery: { delivery: IDelivery; index: number } = null;

  subscription = new Subscription();

  form: FormGroup;
  isSubmitted = false;
  isCancel = false;

  cities: ICity[];
  cityNames: string[];

  deliveryModes: IType[] = deliveryModes;
  estimatedType = estimatedType;

  formPricesLink = null;

  editedIndex = -1;

  selectedMode: DELIVERY_MODE;
  selectedCity: '';

  workingTime = new WorkingTimeMapper();

  editDeliveryId = null;
  
  accidentTimeToggle = false;

  citiesAndRegionsCache: CityAndRegionOption[] = [];
  citiesAndRegions: { [fieldName: string]: CityAndRegionOption[] } = {};

  constructor(
    public modal: NgbActiveModal,
    private deliveryService: DeliveryService,
    private fb: FormBuilder,
    private router: Router,
    private analytics: AnalyticsManagerAbstract
  ) {}

  ngOnInit(): void {
    this.form = this.fb.group({
      isJpost: new FormControl(false, Validators.required),
      direction: new FormGroup({
        from: new FormControl({ cityName: '' }, Validators.required),
      }),
      prices: new FormArray([]),
      deliveryEstimatedDay: new FormControl('', Validators.required),
    });

    this.isInterCity
      ? this.onInterCityFormEstablish()
      : this.onCityFormEstablish();

    this.deliveryService
      .getCitiesAndRegion()
      .subscribe((citiesAndRegions: CityAndRegionOption[]) => {
        this.citiesAndRegionsCache = citiesAndRegions;
        Object.keys((this.form.get('direction') as FormGroup).controls).forEach(
          (fieldName) => {
            this.setDefaultDirectionsOptions(fieldName);
          }
        );
      });

    this.addPrice();
    this.addPrice(true);

    this.formPricesLink = (<FormArray>this.form.get('prices')).controls;
    const firstPriceFromField = this.formPricesLink[0].controls.from;
    firstPriceFromField.setValue('0');
    firstPriceFromField.disable();

    if (this.editDelivery) {
      this.onEdit();
    }
    
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  setDefaultDirectionsOptions(fieldName: string) {
    const regionsCount = 15;
    this.citiesAndRegions[fieldName] = this.citiesAndRegionsCache.slice(
      0,
      regionsCount
    );
  }

  onInterCityFormEstablish() {

    const directions = this.form.get('direction') as FormGroup;
    directions.addControl('to', new FormControl({ cityName: '' }));
    directions.setValue({
      from: { title: '', value: null },
      to: { title: '', value: null },
    });
    Object.keys(directions.controls).forEach((fieldName) => {
      const field = directions.get(fieldName);
      field.valueChanges
        .pipe(debounceTime(200))
        .subscribe((option: InputAutocompleteOption) => {
          if (!option.value) {
            this.citiesAndRegions[fieldName] =
              this.citiesAndRegionsCache.filter(
                ({ title }) =>
                  title.toLowerCase().indexOf(option.title.toLowerCase()) > -1
              );
          } else {
            this.setDefaultDirectionsOptions(fieldName);
          }
          if (option.title === '') {
            this.setDefaultDirectionsOptions(fieldName);
          }
          this.excludeSelectedCityDirections();
        });
    });
  }

  onCityFormEstablish() {

    this.form.get('deliveryEstimatedDay').disable();

    this.form.addControl('isToday', new FormControl(true, Validators.required));
    this.form.addControl(
      'workingTime',
      new FormGroup({
        isDayAndNight: new FormControl('', Validators.required),
        hour: new FormGroup({
          from: new FormControl('', Validators.required),
          to: new FormControl('', Validators.required),
        }),
      })
    );

    this.form.addControl(
      'estimatedTime',
      new FormControl('1300', Validators.required)
    );

    const workingTimeSub = this.form
      .get('workingTime.isDayAndNight')
      .valueChanges.subscribe((value) => {
        const action = value ? 'disable' : 'enable';
        const hourGroup = this.form.get('workingTime.hour') as FormGroup;
        Object.keys(hourGroup.controls).forEach((key) =>
          hourGroup.controls[key][action]()
        );
      });

    this.subscription.add(workingTimeSub);

    const isTodaySub = this.form
      .get('isToday')
      .valueChanges.subscribe((value) => {
        const action = value ? 'enable' : 'disable';
        this.form.get('estimatedTime')[action]();
      });

    this.subscription.add(isTodaySub);
  }

  setAccidentTimeToggle(value: boolean): void {
    this.accidentTimeToggle = value;
    const deliveryEstimatedDay = this.form.get('deliveryEstimatedDay');
    const estimatedTime = this.form.get('estimatedTime');
    const isToday = this.form.get('isToday');

    const [
      dayAction,
      timeAction,
      dayEffect,
    ] = value
      ? [ 'enable', 'disable', () => {} ]
      : [ 'disable', 'enable', () => deliveryEstimatedDay.setValue('') ];

    deliveryEstimatedDay[dayAction]();
    isToday[timeAction]();
    estimatedTime[timeAction]();
    dayEffect();
    isToday.setValue(false);

  }

  excludeSelectedCityDirections() {
    const selectedCityDirections = Object.values(
      (this.form.get('direction') as FormGroup).controls
    )
      .filter(({ value: { value } }) => value && value.type === 3)
      .map(({ value: { title } }) => title);

    this.citiesAndRegions = Object.entries(this.citiesAndRegions).reduce(
      (acc, [fieldName, directions]) => {
        acc[fieldName] = directions.filter(
          ({ title }) => !selectedCityDirections.includes(title)
        );
        return acc;
      },
      {}
    );
  }

  close() {
    this.modal.dismiss();
  }

  setMode(mode: DELIVERY_MODE) {
    this.selectedMode = mode;
  }

  get isModeOwn() {
    return !this.form.get('isJpost').value;
  }

  get isInterCity() {
    return this.router.url.indexOf('intercity') > -1;
  }

  addPrice(canBeFree = false) {
    const isDeliveryTypeCheckbox = canBeFree ? '0' : '';
    const prices = this.form.get('prices') as FormArray;
    const price = new FormGroup({
      from: new FormControl(''),
      to: new FormControl(isDeliveryTypeCheckbox),
      deliveryPrice: new FormControl(isDeliveryTypeCheckbox),
    });
    let index;

    if (prices.length <= 1) {
      prices.push(price);
      index = prices.length;
    } else {
      index = prices.length;
      prices.insert(index - 1, price);
      const nextPrice = prices.at(index);
      price.get('from').setValue(nextPrice.get('from').value);
      nextPrice.get('from').setValue('0');
    }

    if (!canBeFree) {
      const priceSub = price.get('to').valueChanges.subscribe((value) => {
        (prices.at(index) as FormGroup).get('from').setValue(value);
      });
      this.subscription.add(priceSub);
    }
  }

  removePrice(index: number) {
    const prices = this.form.get('prices') as FormArray;
    prices
      .at(index + 1)
      .get('from')
      .setValue(prices.at(index - 1).get('to').value);
    (this.form.get('prices') as FormArray).removeAt(index);
  }

  public getEstimatedDaysSuffix = getRuDays;

  // get disabeEstimatedTime() {
  //   console.log(this.form.get('isToday').value !== 'today')
  //   return this.form.get('isToday').value !== 'today';
  // }

  get formTitle() {
    const formTitleKey = this.editedIndex === -1 ? 'ADD' : 'EDIT';
    return `JMART.SETTINGS.DELIVERY.${formTitleKey}`;
  }

  get cityError() {
    return this.form.get('city').errors;
  }

  onSave() {
    const {
      isJpost,
      direction: rawDirection,
      prices,
      isToday,
      estimatedTime,
      deliveryEstimatedDay,
    } = this.form.getRawValue();

    const isCity = !this.isInterCity;

    const mapDirection = this.isInterCity
      ? ({ value }: CityAndRegionOption) => value
      : (city: ICity) => ({
          ...city,
          id: city.cityId || city.id,
          type: city.type,
        });

    const direction = Object.entries(rawDirection).reduce(
      (acc, [key, value]: [string, ICity & CityAndRegionOption]) => {
        acc[key] = mapDirection(value);
        return acc;
      },
      {}
    );

    const delivery = {
      ...(this.editDeliveryId && {
        id: this.editDeliveryId,
      }),
      isCity,
      isJpost,
      direction,
      attributes: null,
    };

    if (!isJpost) {
      delivery.attributes = {
        prices: prices.map((price) => {
          Object.keys(price).forEach((key) => {
            const value = price[key];
            price[key] =
              typeof price[key] === 'number'
                ? value
                : parseInt(value.replace(/ /g, ''));
          });
          return price;
        }),
        workingTime: this.workingTime.getExternalState(),
        ...(estimatedTime &&
          isToday && {
            estimatedTime: estimatedTime.match(/.{1,2}/g).join(':'),
          }),
        isToday: isToday || false,
        deliveryEstimatedDay: deliveryEstimatedDay || (isToday ? 0 : 1),
      };
    }

    const [action, args] = this.editDelivery
      ? ['editDelivery', [delivery, this.editDeliveryId]]
      : ['addDelivery', [delivery]];

    if (action === 'addDelivery') {
      this.analytics.logEvent('JMartDeliveryAdd_Save_Click', {
        version: 1,
        region: this.deliveryService.selectedDelivery,
        type: isJpost ? 'jpost' : 'client',
      });
    }

    this.deliveryService[action](...args);
    this.close();
  }

  onEdit() {
    this.editDeliveryId = this.editDelivery.delivery.id;

    const {
      isJpost,
      direction: { from, to },
      attributes: {
        isToday,
        estimatedTime,
        workingTime,
        prices,
        deliveryEstimatedDay,
      },
    } = this.editDelivery.delivery;

    const direction = this.isInterCity
      ? {
          from: { title: from.title, value: from },
          to: { title: to.title, value: to },
        }
      : {
          from,
        };

    const form = {
      isJpost,
      isToday,
      direction,
      ...(!isJpost && {
        ...(estimatedTime && {
          estimatedTime: estimatedTime.split(':').join(''),
        }),
        workingTime: {
          hour: {
            to: workingTime[0].hour.to,
            from: workingTime[0].hour.from,
          },
          isDayAndNight: [
            { key: 'to', value: '23:59' },
            { key: 'from', value: '00:00' },
          ].every(({ key, value }) => workingTime[0].hour[key] === value),
        },
        prices,
        ...(deliveryEstimatedDay && { deliveryEstimatedDay }),
      }),
    };

    if (!isJpost) {
      const pricesDiff =
        prices.length - (this.form.get('prices') as FormArray).length;

      if (pricesDiff > 0) {
        for (let i = 0; i < pricesDiff; i++) {
          this.addPrice();
        }
      }

      if (workingTime) {
        this.workingTime = new WorkingTimeMapper(workingTime);
      }
    }

    // this.form.setValue(form);
    this.form.patchValue(form);
    setTimeout(() => {
      if (
        (parseInt(this.editDelivery.delivery.attributes.deliveryEstimatedDay) > 1)
      ) {
        this.setAccidentTimeToggle(true);
      }
      this.form.get('isJpost').setValue(isJpost);
    }, 0);
  }
}
