import { Component, OnDestroy, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { ModalsService, PickupPointsService } from '../../services';
import { combineLatest, Subscription } from 'rxjs';
import { pointsTypes } from '../../data/pickup-points-mock';
import { map, startWith } from 'rxjs/operators';
import { FILTER_POINT_STATUS, IPoint, IPointStatus } from '../../models/pickup-points.model';

@Component({
  selector: 'j-pickup-points',
  templateUrl: './pickup-points.component.html',
  styleUrls: ['./pickup-points.component.scss']
})
export class PickupPointsComponent implements OnInit, OnDestroy {
  form: FormGroup;
  search = new FormControl('');
  points: IPoint[];
  translatePrefix = 'JMART.SETTINGS.PICKUPPOINTS';
  isLoading = true;
  statuses: IPointStatus[];
  sSub: Subscription;
  pSub = new Subscription();
  cities: any[];

  constructor(
    private modalsService: ModalsService,
    private pointsService: PickupPointsService
    ) {
    this.form = new FormGroup({ status: new FormControl('all')});

    this.sSub = this.pointsService
      .getStatuses()
      .subscribe((statuses) => {
        this.statuses = statuses.slice(0, 3);
      });
  }

  ngOnInit(): void {
    this.getCities();
    const points$ = this.pointsService.getPoints();

    this.pSub.add(
      combineLatest([points$, this.search.valueChanges.pipe(startWith('')), this.form.get('status').valueChanges.pipe(startWith('all'))])
        .pipe(
          map(([points, search, status]) => {
            let filteredData;
            if(status !== 'all') {
              filteredData = points.filter((point) => point.type === status);
            } else {
              filteredData = points;
            }

            let searchedData = filteredData.filter(d => {
              return (d.companyStoreId && d.companyStoreId.toLowerCase().includes(search.toLowerCase()))
                || d.city.name.toLowerCase().includes(search.toLowerCase())
                || d.pickupAddress.toLowerCase().includes(search.toLowerCase())
            });

            return searchedData.map(el => ({
              ...el,
              workingTime: el.workingTime
                ?  el.workingTime.map(t => {
                    return {
                      days: t.days.map(d => d.day).join(', '),
                      hour: t.hour.from.localeCompare('00:00') === 0 && t.hour.to.localeCompare('00:00') === 0
                        ? ' '
                        : `${t.hour.from} - ${t.hour.to}`,
                      break: t.break.from.localeCompare('00:00') === 0  && t.break.to.localeCompare('00:00') === 0
                        ? '  '
                        : ` ${t.break.from} - ${t.break.to}`,
                    }
                  })
                : '',
              type: pointsTypes.find(p => {
                if(p.value === el.type) return p.label
              }),
              isMain: el.isMain === 'Y' ? '   ' : '',
              isMainInCity: el.isMainInCity === 'Y' ? '   ' : ''
            }))
          })
        )
        .subscribe(
          points => { this.points = points; }
        )
    );

    this.pointsService.onPointsChanged.subscribe(
      (({ action, id, point = null }) => {
        const reaction = {
          add: 'handleAdd',
          edit: 'handleEdit',
          delete: 'handleDelete',
        }[action];
        this[reaction](id, point)
        // handle logic here
        // don't forget to unsubscribe, also use Subacription if you multiple subs for teardown
      })
    )
  }

  handleAdd(id: number, point: IPoint) {
    const modifiedPoint = this.mapPoint(point);
    this.points.push(modifiedPoint);
  }

  handleEdit(id: number, point: IPoint) {
    const index = this.points.findIndex(
      ({ storeLocationId }) => id === storeLocationId
    );
    const modifiedPoint = this.mapPoint(point);
    this.points[index] = modifiedPoint;
  }

  handleDelete(id: number, _) {
    const index = this.points.findIndex(
      ({ storeLocationId }) => id === storeLocationId
    );
    this.points.splice(index, 1);
  }

  ngOnDestroy(): void {
    if(this.pSub) {
      this.pSub.unsubscribe();
    }
    if(this.sSub) {
      this.sSub.unsubscribe();
    }
  }

  addPickupPoints() {
    this.modalsService.pickupPointsModal();
  }

  get selected() {
    return this.statuses.find((status) => {
      return status.isSelected === true;
    });
  }

  onSelect(status: FILTER_POINT_STATUS) {
    this.form.get('status').patchValue(status);
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  getCities() {
    this.pointsService.getCities().subscribe(city => {
      this.cities = city;
    });
  }

  mapPoint(el) {
    return  {
        ...el,
        workingTime: el.workingTime
          ?  el.workingTime.map(t => {
              return {
                days: t.days.map(d => d.day).join(', '),
                hour: t.hour.from.localeCompare('00:00') === 0 && t.hour.to.localeCompare('00:00') === 0
                  ? ' '
                  : `${t.hour.from} - ${t.hour.to}`,
                break: t.break.from.localeCompare('00:00') === 0  && t.break.to.localeCompare('00:00') === 0
                  ? '  '
                  : ` ${t.break.from} - ${t.break.to}`,
              }
            })
          : '',
        type: pointsTypes.find(p => {
          if(p.value === el.type) return p.label
        }),
        isMain: el.isMain === 'Y' ? '   ' : '',
        isMainInCity: el.isMainInCity === 'Y' ? '   ' : '',
        city: this.cities.find(el => el.id === el.cityId)
      }
  }
}
