import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ResponsiblePersonsService } from '../../../services/information/responsible-persons/responsible-persons.service';

@Component({
  selector: 'j-remove-modal',
  templateUrl: './remove-modal.component.html',
  styleUrls: ['./remove-modal.component.scss']
})
export class RemoveModalComponent implements OnInit {
  @Input() blockId: number;
  @Input() id: number;

  constructor(private modal: NgbActiveModal, private resPersonsService: ResponsiblePersonsService) { }

  ngOnInit(): void {
  }

  remove() {
    this.resPersonsService.remove(this.blockId, this.id);
    this.modal.dismiss();
  }

  cancel() {
    this.modal.dismiss();
  }

}
