import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl } from '@angular/forms';

export type DeliveryToggleMode = {
  value: boolean,
  label: string
};

export type DeliveryToggleModesTuple = [
  DeliveryToggleMode,
  DeliveryToggleMode,
];

@Component({
  selector: 'j-delivery-mode-toggle',
  templateUrl: './delivery-mode-toggle.component.html',
  styleUrls: ['./delivery-mode-toggle.component.scss']
})
export class DeliveryModeToggleComponent implements OnInit {

  @Input() control = new FormControl();

  constructor() {}

  mods: DeliveryToggleModesTuple = [
    {
      value: true,
      label: ' JPost',
    },
    {
      value: false,
      label: ' ',
    },
  ]

  ngOnInit(): void {
    // this.selectedMode = this.mods[0];
    this.control.setValue(this.mods[1].value);
  }

  setMod({ value }: DeliveryToggleMode) {
    this.control.setValue(value);
  }

}
