import { AfterContentInit, Component, ContentChildren, Input, OnInit, QueryList } from '@angular/core';
import { GetTemplateDirective } from './get-template.directive';

// TODO probably we need generic type here instead of any
// but the question is how we pass generic to input
export type JTableData = {
  columns: any[],
  rows: Array<any[]>,
};

export type JTableClasses = Partial<{
  table: string,
  th: string,
  td: string,
}>;

@Component({
  selector: 'j-table',
  templateUrl: './j-table.component.html',
  styleUrls: ['./j-table.component.scss']
})
export class JTableComponent implements OnInit, AfterContentInit {

  @ContentChildren(GetTemplateDirective) templates: QueryList<GetTemplateDirective>;

  @Input() data: JTableData;
  @Input() class: JTableClasses = {};

  tpls;

  constructor() { }

  ngOnInit(): void {
    
  }

  ngAfterContentInit() {
    this.tpls = this.templates.toArray().reduce(
      (acc, { name, template }) => {
        acc[name] = template;
        return acc;
      },
      {}
    );
  }

  getColumnTplsByIndex(i: number) {
    return this.tpls[`column-${i + 1}`];
  }

}
