import { Injectable } from '@angular/core';
import { ResponsiblePersonsAbstract } from './responsible-persons.abstract';
import { IResponsablePersonsBlock } from '../../../models';
import { responsiblePersons } from '../../../data/information-mock';

@Injectable({
  providedIn: 'root'
})
export class ResponsiblePersonsService extends ResponsiblePersonsAbstract {
  rPersons: IResponsablePersonsBlock[] = responsiblePersons;

  constructor() {
    super();
  }

  get(): IResponsablePersonsBlock[] {
    return this.rPersons;
  }


   create(person: IResponsablePersonsBlock) {
     this.rPersons.map(block => {
       if (block.id === person.id) {
         block.persons.push(person.persons[0]);
       }
     });
  }

  edit(person: IResponsablePersonsBlock) {
    let objIndex;
    this.rPersons.map(block => {
      if(block.id === person.id) {
        objIndex = block.persons.findIndex(p => p.id === person.persons[0].id);
        block.persons[objIndex].lastName = person.persons[0].lastName;
        block.persons[objIndex].firstName = person.persons[0].firstName;
        block.persons[objIndex].patronymic = person.persons[0].patronymic;
        block.persons[objIndex].emails = person.persons[0].emails;
        block.persons[objIndex].phones = person.persons[0].phones;
      }
    });
  }

  remove(blockId: number, id: number) {
    let objIndex;
    this.rPersons.map(block => {
      if(block.id === blockId) {
        objIndex = block.persons.findIndex(p => p.id === id);
        if (objIndex > -1) {
          block.persons.splice(objIndex, 1);
        }
      }
    });
  }
}
