import { ITime } from './pickup-points.model';

export enum DELIVERY_CATEGORY {
  BY_CITY = 'BY_CITY',
  BY_INTERCITY = 'BY_INTERCITY'
}

export enum DELIVERY_MODE {
  OWN = 'own',
  JPOST = 'jpost'
}

export interface IPrice {
  from: number | string,
  to: number | string,
  deliveryPrice: number | string
}

export interface IType {
  value:  string,
  label: string,
}

export interface ICity {
  cityId: number,
  title: string;
  id?: number,
  type?: number,
};

export interface IDelivery {
  id?: number,
  isCity: boolean,
  isJpost: boolean,
  direction: {
    from: ICity,
    to?: ICity, 
  },
  attributes: {
    prices: IPrice[],
    workingTime: ITime[],
    estimatedTime: string,
    isToday: boolean,
    deliveryEstimatedDay?: string
  },
}

interface IDeliveryInterCity extends IDelivery {
  deliveryEstimatedDay: number,
}

