import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PickupPointsService } from '@jmart-cabinet/modules/settings/services/pickup-points.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-points-remove-modal',
  templateUrl: './points-remove-modal.component.html',
  styleUrls: ['./points-remove-modal.component.scss']
})
export class PointsRemoveModalComponent implements OnInit {
  @Input() storeLocationId: number;

  constructor(private modal: NgbActiveModal, private pointService: PickupPointsService) { }

  ngOnInit(): void {
  }

  remove() {
    this.pointService.remove(this.storeLocationId).subscribe(
      () => {
        this.pointService.onPointsChanged.next({
          id: this.storeLocationId,
          action: 'delete'
        });
        this.modal.dismiss();
      },
      err => {
        // handle error
      }
    );
  }

  cancel() {
    this.modal.dismiss();
  }
}
