import { Component, Input, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { IResponsablePerson, IResponsablePersonsBlock } from '../../../models';
import { ModalsService } from '../../../services/modals.service';
import { ResponsiblePersonsService } from '../../../services/information/responsible-persons/responsible-persons.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-jmart-settings-add-modal',
  templateUrl: './add-edit-modal.component.html',
  styleUrls: ['./add-edit-modal.component.scss']
})
export class AddEditModalComponent implements OnInit {
  @Input() public blockId;
  @Input() public info: IResponsablePerson;
  form: FormGroup;
  isSubmitted = false;
  limit = 4;
  editedIndex = -1;

  constructor(
    private modal: NgbActiveModal,
    private resPersonsService: ResponsiblePersonsService,
    private modalsService: ModalsService,
    private fb: FormBuilder,
  ) {
    /* Form initialization */
    this.form = this.fb.group({
      lastName: new FormControl('', Validators.required),
      firstName: new FormControl('', Validators.required),
      patronymic: new FormControl(''),
      emails:  this.fb.array([
        this.fb.group({
          email: new FormControl('', [Validators.required, Validators.email])
        })
      ]),
      phones: this.fb.array([
        this.fb.group({
          phone: new FormControl('', Validators.required)
        })
      ])
    });
  }

  ngOnInit(): void {
    /* Filling in a form */
    if(this.info) {
      this.editedIndex = 1;
      this.form.patchValue({
        lastName: this.info.lastName,
        firstName: this.info.firstName,
        patronymic: this.info.patronymic,
      });
      this.info.emails.forEach((el, index) => {
        if(index > 0) this.addEmail();
        this.emails().at(index).get('email').patchValue(el);
      });
      this.info.phones.forEach((el, index) => {
        if(index > 0) this.addPhone();
        this.phones().at(index).get('phone').patchValue(el);
      });
    }
  }

  get formTitle () {
    return this.editedIndex === -1 ? 'JMART.SETTINGS.INFORMATION.ADD_RESP_PERSON' : 'JMART.SETTINGS.INFORMATION.EDIT_RESP_PERSON'
  }

  get lastNameError() {
    return this.form.get('lastName').errors;
  }

  get fistNameError() {
    return this.form.get('firstName').errors;
  }

  /* Phones */
  phones(): FormArray {
    return this.form.get('phones') as FormArray;
  }

  newPhone(): FormGroup {
    return this.fb.group({
      phone: new FormControl('', Validators.required)
    });
  }

  addPhone() {
    if(this.phones().length > this.limit) {
      return;
    }
    this.phones().push(this.newPhone());
  }

  removePhone(i: number) {
    this.phones().removeAt(i);
  }

  phoneError(i) {
    // @ts-ignore
    return (<FormArray>this.form.get('phones')).controls[i].controls.phone.errors;
  }

  /* Emails */
  emails(): FormArray {
    return this.form.get('emails') as FormArray;
  }

  newEmail(): FormGroup {
    return this.fb.group({
      email: new FormControl('', [Validators.required, Validators.email])
    });
  }

  addEmail() {
    if(this.emails().length > this.limit) {
      return;
    }
    this.emails().push(this.newEmail());
  }

  removeEmail(i: number) {
    this.emails().removeAt(i);
  }

  emailError(i) {
    // @ts-ignore
    return (<FormArray>this.form.get('emails')).controls[i].controls.email.errors;
  }

  /*Add person*/
  addPerson() {
    this.isSubmitted = true;
    if(this.form.invalid) {
      return;
    }

    if(this.editedIndex === -1) {
      const person: IResponsablePersonsBlock = {
        id: this.blockId,
        persons: [
          {
            lastName: this.form.value.lastName,
            firstName: this.form.value.firstName,
            patronymic: this.form.value.patronymic,
            emails: this.form.value.emails.map(el => el.email),
            phones: this.form.value.phones.map(el => el.phone)
          }
        ]
      };
      this.resPersonsService.create(person);
    } else {
      const person: IResponsablePersonsBlock = {
        id: this.blockId,
        persons: [
          {
            id: this.info.id,
            lastName: this.form.value.lastName,
            firstName: this.form.value.firstName,
            patronymic: this.form.value.patronymic,
            emails: this.form.value.emails.map(el => el.email),
            phones: this.form.value.phones.map(el => el.phone)
          }
        ]
      };
      this.resPersonsService.edit(person);
    }
    this.cancel();
  }

  removePerson() {
    this.modalsService.removePersonModal(this.blockId, this.info.id);
    this.cancel();
  }

  formReset() {
    this.form.get('lastName').reset();
    this.form.get('firstName').reset();
    this.form.get('patronymic').reset();
    (this.form.controls['emails']).reset();
    (this.form.controls['phones']).reset();
  }

  cancel() {
    this.formReset();
    this.editedIndex = -1;
    this.modal.dismiss();
  }
}
