import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { CompanyService } from '@company/company.service';
import { ModalsService } from '../../services';
import { Subscription } from 'rxjs';

@Component({
  selector: 'j-information-main',
  templateUrl: './information-main.component.html',
  styleUrls: ['./information-main.component.scss']
})
export class InformationMainComponent implements OnInit, OnDestroy {
  logo: string;
  companyName: FormControl;
  fSub: Subscription;
  cSub: Subscription;

  constructor(private companyService: CompanyService, public modalService: ModalsService) { }

  ngOnInit(): void {
    this.fSub = this.modalService.uploadedLogo$.subscribe(el => {
      if(el) {
        const file = el;
        const reader = new FileReader();
        reader.onload = e => this.logo = reader.result.toString();
        reader.readAsDataURL(file);
      } else {
        this.logo = '';
      }
    });

    this.cSub = this.companyService.currentAuthority$.subscribe(el => {
      // dev returns undefined
      // this.companyName = new FormControl({ value: el.company.shortName, disabled: true });
      this.companyName = new FormControl({value: 'TOO Tech and Friends', disabled: true});
    });
  }

  ngOnDestroy(): void {
    if(this.fSub) {
      this.fSub.unsubscribe();
    }

    if(this.cSub) {
      this.cSub.unsubscribe();
    }
  }
}
