import { Component, Input, OnInit } from '@angular/core';
import { AddEditModalComponent } from '../../modals';
import { IResponsablePersonsBlock } from '../../models';
import { ModalsService } from '../../services';

@Component({
  selector: 'j-information-res-persons',
  templateUrl: './information-res-persons.component.html',
  styleUrls: ['./information-res-persons.component.scss']
})
export class InformationResPersonsComponent implements OnInit {


  @Input() respPersons: IResponsablePersonsBlock[];

  constructor(private modalsService: ModalsService) { }

  ngOnInit(): void {
  }

  identify(index, item) {
    return item.id;
  }

  addPerson(blockId) {
    this.modalsService.addEditPersonModal(AddEditModalComponent, blockId);
  }
}
