import { Component, OnInit } from '@angular/core';
import { DELIVERY_MODE } from '@jmart-cabinet/modules/settings/models/delivery.model';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DeliveryModalComponent } from '../delivery-modal/delivery-modal.component';

@Component({
  selector: 'j-set-delivery-modal',
  templateUrl: './set-delivery-modal.component.html',
  styleUrls: ['./set-delivery-modal.component.scss']
})
export class SetDeliveryModalComponent implements OnInit {
  modalSmParams = {
    backdropClass: 'backdrop_light',
    windowClass: 'modal_light modal_dictionary',
    centered: true
  };

  constructor(public modal: NgbActiveModal, private ngbModalService: NgbModal) { }

  ngOnInit(): void { }

  close() {
    this.modal.dismiss();
  }

  setJpost() {
    const modalRef = this.ngbModalService.open(DeliveryModalComponent, this.modalSmParams);
    modalRef.componentInstance.mode = DELIVERY_MODE.JPOST;
    this.modal.dismiss();
  }

  setOwnDelivery() {
    const modalRef = this.ngbModalService.open(DeliveryModalComponent, this.modalSmParams);
    modalRef.componentInstance.mode = DELIVERY_MODE.OWN;
    this.modal.dismiss();
  }
}
