import { Component, OnInit } from '@angular/core';
import { ModalsService } from '@jmart-cabinet/modules/settings/services/modals.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-notice-modal',
  templateUrl: './notice-modal.component.html',
  styleUrls: ['./notice-modal.component.scss']
})
export class NoticeModalComponent implements OnInit {

  constructor(public modal: NgbActiveModal, private modalsService: ModalsService) { }

  ngOnInit(): void {
  }

  close() {
    localStorage.removeItem('form');
    this.modal.dismiss();
  }

  cancel() {
    this.modal.dismiss();
    this.modalsService.pickupPointsModal();
  }
}
